  methods: {
    init(date) {
      get_http('zdyztfxSzxjjajScfxbg', {
        zt: this.zt,
        day: `[${date.startDate},${date.endDate}]`,
      })
      .then(res => {
        this.zdyztfxSzxjjajScfxbg = res.data.data;
        console.log(res.data.data);
      })
      .catch(err => {
        console.log(err);
      });
    }
  },
  computed: {
    date() {
      return this.$store.state.date;
    }
  },
  watch: {
    date(val) {
      this.init(val);
    }
  }
};
</script>

